.App {
  text-align: center;
}


.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Wrapper{
  background: #000;
  padding: 10px;
  /* border-radius: 10%; */

} 

.Input-wrapper{
  display: flex;
  justify-content: space-between;
  width: 300px;
  align-items: center;
  margin: 20px;
}

.Input-wrapper input{
  /* border-radius: 4%; */
  border: 0;
  background-color: #ede7fe;
  width: 250px;
  padding: 6px;
  height: 30px;
  font-size: 20px;
}

.Input-wrapper button{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  padding: 0;
  border: 0;
}

h3{
  margin: 0;
  padding: 0;
}